
@use "@styles/variables" as *
@use "@styles/mixins" as *

.about-section
	background-color: $blue-dark
	background-image: url('@images/bg.jpg')
	background-image: image-set(url('@images/bg.webp') type('image/webp'), url('@images/bg.jpg') type('image/jpeg'))
	background-repeat: no-repeat
	background-size: 100% 100%
	background-position: center
	background-size: cover
	padding: 120px 0 70px

	@include screen(sm)
		background-image: url('@images/bg-mobile.jpg')
		background-image: image-set(url('@images/bg-mobile.webp') type('image/webp'), url('@images/bg-mobile.jpg') type('image/jpeg'))
		padding: 70px 0 40px

.about-section__grid
	display: grid
	grid-template-columns:  284px 1fr
	grid-template-areas: 'portrait heading' 'portrait story'
	grid-column-gap: 56px
	width: 100%
	margin: 0 auto

	@include screen(sm)
		grid-template-columns:  1fr 1fr
		grid-template-areas: 'portrait heading' 'story story'
		grid-column-gap: 26px
		grid-row-gap: 22px

	@include screen(xs)
		grid-template-columns:  95px 1fr

.about-section__portrait
	grid-area: portrait
	justify-self: end

	picture
		display: block
		line-height: 0

	img
		width: 100%
		height: auto
		@include gradient-border-radius

		@include screen(sm)
			width: 95px

.about-section__story-heading
	grid-area: heading
	font-family: $font-sf
	font-weight: 600
	font-size: 48px
	line-height: 57px
	text-align: left
	color: $white
	margin: 0 0 24px

	br
		display: none

	@include screen(sm)
		align-self: center
		font-size: 24px
		line-height: normal
		margin: 0

		br
			display: inline

	@include screen(xs)
		align-self: end

.about-section__story-text-wrapper
	grid-area: story

.about-section__story-text
	font-family: $font-sf
	font-weight: 400
	font-size: 18px
	line-height: 27px
	letter-spacing: -0.03em
	text-align: left
	color: $white
	margin: 0

	&:not(:last-child)
		margin-bottom: 16px

	a
		color: $orange

		&:visited
			color: $orange

	@include screen(sm)
		font-size: 14px
		line-height: 24px

		&:not(:last-child)
			margin-bottom: 8px

.recent-section
	padding: 50px 0

.recent-section__list
	display: grid
	grid-template-columns:  1fr 1fr 1fr
	grid-column-gap: 20px
	grid-row-gap: 36px
	list-style: none
	margin: 0px
	padding: 0px

	@include screen(sm)
		grid-template-columns:  1fr 1fr

	@include screen(xs)
		grid-template-columns:  1fr
